import urls from '../../utils/urls.js'
export default {
	data() {
		return {
			bookList: [],
			isLogin: this.$store.state.isLogin
		};
	},
	onShow() {
		let user = uni.getStorageSync('userInfo')
		let id = ''
		if (user) {
			id = JSON.parse(user).id
			console.log(id)
			uni.request({
				url: urls.HTTP + '/index/userbookshelf',
				method: 'POST',
				data: {
					id: id
				},
				success: (res) => {
					console.log(res)
					this.bookList = res.data.userinfo.data
				}
			})
		}
	},
	methods: {
		goSearch() {
			uni.navigateTo({
				url: 'searchBook'
			})
		},
		enterBook: function(item) {
			let id = item.chapter_id
			let key = item.chapter_key
			
			let id1 = '';
			if(this.isLogin){
				id1 = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id1 = 0
			}
			
			// let uid = JSON.parse(uni.getStorageSync('userInfo')).id
			uni.request({
				url: urls.HTTP + '/novel/getnovelcontent',
				method: 'POST',
				data: {
					id: id,
					key: key,
					uid: id1
				},
				success: (res) => {
					// console.log('进入阅读', res)
					this.$store.dispatch('storyData', res.data)
					uni.navigateTo({
						url: '../common/book/story'
					})
				}
			})
		},
		add: function() {
			uni.switchTab({
				url: '../find/find'
			})
		},
		deleteBook: function(item) {
			let novelid = item.novel_id
			let id = '';
			if(this.isLogin){
				id = JSON.parse(uni.getStorageSync('userInfo')).id
			}else{
				 id = 0
			}
			// let uid = JSON.parse(uni.getStorageSync('userInfo')).id
			uni.showModal({
				title: '提示',
				content: '是否删除本书？',
				success: (res) => {
					if (res.confirm) {
						uni.request({
							url: urls.HTTP + '/Bookshelf/delbookshelf',
							method: 'POST',
							data: {
								novelid: novelid,
								uid: id
							},
							success: (res) => {
								// console.log('delete', res)
								if (res.data.code === 0) {
									this.bookList = res.data.bookshelf.data
									uni.showToast({
										title: '删除成功'
									})
								} else {
									uni.showToast({
										title: res.data.code,
										icon: 'none'
									})
								}
							}
						})
					}
				}
			})
		}
	}
}
